* {
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  padding: 0;
  max-width: 900px;
  text-align: center;
  background: #fddd31;
}

h1 {
  font-family: "Work Sans", sans-serif;
}

#pitch {
  font-family: "Work Sans", sans-serif;
  font-size: 14rem;
}

#tuner {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
}
